<template>
  <div class="user">
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-row>
        <el-col :span="6">
          <el-input v-model="queryInfo.query" placeholder="请输入内容">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="6" class="addsb">
          <el-button type="primary" @click="addUserDia = true">添加用户</el-button>
        </el-col>
      </el-row>
      <!-- 表格组件 -->
      <el-table :data="usersList" style="width: 100%" border>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="username" label="姓名"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="mobile" label="电话"></el-table-column>
        <el-table-column prop="role_name" label="角色"></el-table-column>
        <el-table-column prop="mg_state" label="状态">
          <template slot-scope="scope">
            <!-- {{scope.row}} -->
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStatus(scope.row.id,scope.row.mg_state,scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="sco">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="getAlertUser(sco.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUser(sco.row.id)"
            ></el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="getQuanxian(sco.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[2, 5, 10, 15]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!-- 添加用户弹出框 -->
      <el-dialog title="添加用户" :visible.sync="addUserDia" width="50%" @closed="diaClosed('addRef')">
        <el-form :model="addForm" :rules="addRul" ref="addRef">
          <el-form-item label="用户名" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="手机" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addUserDia = false">取 消</el-button>
          <el-button type="primary" @click="addUsers">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 修改弹出框 -->
      <el-dialog title="修改用户" :visible.sync="alertUserDia" width="50%" @closed="diaClosed('alterRef')">
        <el-form :model="alertUserFrom" :rules="addRul" ref="alterRef">
          <el-form-item label="username">
            <el-input v-model="alertUserFrom.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="手机" prop="mobile">
            <el-input v-model="alertUserFrom.mobile"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="alertUserFrom.email"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="alertUserDia = false">取 消</el-button>
          <el-button type="primary" @click="alertUser">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 分配权限 -->
      <el-dialog title="重新分配权限" :visible.sync="quanxianDia" width="30%">
          <p>当前的用户：{{assUserInfo.username}}</p>
          <p>当前的角色：{{assUserInfo.role_name}}</p>
          <p>分配新角色
        <el-select v-model="assionVal" placeholder="请选择">
          <el-option v-for="item in assList" :key="item.id" :label="item.roleName" :value="item.id"></el-option>
        </el-select>
         </p>
        <span slot="footer" class="dialog-footer">
          <el-button @click="quanxianDia = false">取 消</el-button>
          <el-button type="primary" @click="alertQuanxian">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //表格数据
      usersList: [],
      //传送提交请求
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 2
      },
      msStatus: true,
      total: 0,
      //   添加用户弹出框
      addUserDia: false,
      //   添加用户数据
      addForm: {
        id: "",
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      //   添加数据规则
      addRul: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        email: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
        mobile: [{ required: true, message: "请输入手机号码", trigger: "blur" }]
      },
      alertUserDia: false,
      alertUserFrom: {
        email: "",
        mobile: ""
      },
      //   权限弹框
      quanxianDia: false,
      //   权限列表
      assList: [],
      //   权限选择值
      assionVal: "",
      //   选择的用户信息
      assUserInfo: {}
    };
  },
  methods: {
 
    //   获取用户列表
    async getUserList() {
      let { data: res } = await this.$http.get("users", {
        params: this.queryInfo
      });
      if (res.meta.status != 200) {
        return this.$message.error("获取用户列表失败");
      }

      this.total = res.data.total;
      this.usersList = res.data.users;
    },
    // 切换每页显示多少条
    handleSizeChange(size) {
      this.queryInfo.pagesize = size;
      this.getUserList();
    },
    // 切页
    handleCurrentChange(num) {
      this.queryInfo.pagenum = num;
      this.getUserList();
    },
    // 添加数据
    addUsers() {
      this.$refs.addRef.validate(async valid => {
        if (!valid) return;
        let { data: res } = await this.$http.post("users", this.addForm);

        if (res.meta.status != 201) {
          return this.$message.error(res.meta.msg);
        }

        this.addUserDia = false;
        this.$message.success("添加成功");
        this.getUserList();
      });
    },
    // 修改用户状态
    async userStatus(id, type, user) {
      let { data: res } = await this.$http.put(`users/${id}/state/${type}`);
      if (res.meta.status != 200) {
        user.mg_state = !user.mg_state;
        return this.$message.error("设置失败");
      }
      this.$message.success("修改成功");
    },
    // 获取修改的用户信息
    async getAlertUser(id) {
      this.alertUserDia = true;
      let { data: res } = await this.$http.get("users/" + id);
      if (res.meta.status != 200) {
        return this.$message.error("获取用户失败");
      }
      this.alertUserFrom = res.data;
    },
    // 上传修改后的用户信息
    async alertUser() {
      let { data: res } = await this.$http.put(
        "users/" + this.alertUserFrom.id,
        this.alertUserFrom
      );
      if (res.meta.status != 200) {
        return this.$message.error("修改失败");
      }
      this.alertUserDia = false;
      this.$message.success("修改成功");
      this.getUserList();
    },
    // 删除用户
    async deleteUser(id) {
      let { data: res } = await this.$http.delete("users/" + id);
      if (res.meta.status != 200) {
        return this.$message.error("删除用户失败");
      }

      this.$message.success("删除用户成功");
      this.getUserList();
    },
    // 获取用户信息
    async getQuanxian(user) {
        this.assUserInfo = user;
      this.quanxianDia = true;
      let { data: res } = await this.$http.get("roles");
      if (res.meta.status != 200) {
        return this.$message.error("权限获取失败");
      }
      this.assList = res.data;
    },
    // 修改权限
    async alertQuanxian() {
       let {data:res} = await this.$http.put(`users/${this.assUserInfo.id}/role`,{
           rid:this.assionVal
       })
       if(res.meta.status != 200){
           return this.$message,error("权限修改失败")
       }
       this.quanxianDia = false;
       this.$message.success("权限修改成功")
       this.getUserList()
    }
  },
  created: function() {
    this.getUserList();
  }
};
</script>

<style>
</style>
